@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap');
*{
    box-sizing: border-box;
}
body{
    background-color: #1E2931;
    font-family: 'Roboto', sans-serif;
    color: #fff;
}
.mainContainer{
    padding: 2% 4%;
}
h1{
    text-align: center;
    font-size: 30px;
    margin: 0;
}
.searchContainer{
    max-width: 1000px;
    margin: 10px auto;
    margin-bottom: 50px;
}
.searchBox{
    width: 100%;
    outline: none;
    padding: 10px;
    font-size: 16px;
    background-color: #131B20;
    text-align: center;
    border: 1px solid #fff;
    color: #fff;
}
.moviesContainer{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.moviesCards{
    margin: 20px;
    width: 200px;
    position: relative;
}
.moviePoster{
    width: 100%;
    height: 100%;
}
.titleContainer{
    position: absolute;
    top: 0;
    width: 200px;
    height: 100%;
    background-color: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5%;
    text-align: center;
    display: none;
}
.moviesCards:hover{
    transform: scale(1.1,1.1);
}
.moviesCards:hover .titleContainer{
    background-color: #000;
    opacity: 0.7;
    display: flex;
}
.title{
    font-size: 24px;
    margin: 0;
}